// URL prefix to use with direct internal links
let serverUrl = new URL(process.env.REACT_APP_PORTAL || "http://localhost");
serverUrl.pathname = process.env.REACT_APP_BASENAME;
const PORTAL_BASE_URL = serverUrl;
  

// regular expressions to match cuid IDs (https://github.com/ericelliott/cuid)
export const CUID_REG_STR = "c[a-z0-9]{24}"; // ckhz90ali0003p7pvhhre44cg

// RegExp object:
export const CUID_REGEXP = new RegExp(CUID_REG_STR);

// utility function to check RegExp
export const checkSlug = slug => CUID_REGEXP.exec(slug);

export const createSlugUrl = slug => `${PORTAL_BASE_URL.href}m/${slug}`;
export const createSlugUrlForGuests = slug =>
  `${PORTAL_BASE_URL}m/${slug}/guest`;
