import React, { createContext, useReducer } from "react";
import * as langs from "./const.js";

import messages_de from "./messages.de.js";
import messages_en from "./messages.en.js";
import messages_fr from "./messages.fr.js";

const I18nStateContext = createContext();
const I18nDispatchContext = createContext();

const BROWSER_LANGUAGE =
  (navigator.languages && navigator.languages[0]) ||
  navigator.language ||
  navigator.userLanguage ||
  langs.LANG_DEFAULT;

const i18nReducer = (_, lang) => {
  switch (lang.value) {
    case langs.LANG_DE:
    case langs.LANG_DE_DE:
    case langs.LANG_DE_AT:
    case langs.LANG_DE_CH:
    case langs.LANG_DE_LI:
    case langs.LANG_DE_LU: {
      return { lang, messages: messages_de };
    }
    case langs.LANG_EN:
    case langs.LANG_EN_US:
    case langs.LANG_EN_GB: {
      return { lang, messages: messages_en };
    }
    case langs.LANG_FR:
    case langs.LANG_FR_BE:
    case langs.LANG_FR_CH: 
    case langs.LANG_FR_FR: 
    case langs.LANG_FR_LU: 
    case langs.LANG_FR_MC: {
      return { lang, messages: messages_fr };
    }
    default: {
      throw new Error(`Unhandled i18n language: ${JSON.stringify(lang.value)}`);
    }
  }
};

const defaultState = {
  lang: BROWSER_LANGUAGE,
  messages: messages_en
};

const I18nProvider = ({ children }) => {
  const [state, dispatch] = useReducer(i18nReducer, defaultState);
  return (
    <I18nStateContext.Provider value={state}>
      <I18nDispatchContext.Provider value={dispatch}>
        {children}
      </I18nDispatchContext.Provider>
    </I18nStateContext.Provider>
  );
};

const useI18nState = () => {
  const context = React.useContext(I18nStateContext);
  if (context === undefined) {
    throw new Error("useI18nState must be used within a I18nProvider");
  }
  return context;
};

const useI18nDispatch = () => {
  const context = React.useContext(I18nDispatchContext);
  if (context === undefined) {
    throw new Error("useI18nDispatch must be used within a I18nProvider");
  }
  return context;
};

export { I18nProvider, I18nStateContext, useI18nState, useI18nDispatch };
