
/*
 * Opens a new tab with the Jitsi-Meet page.
 * Sends a message with the meeting URL so the invite mechanism can use that.
 * Returns true if tab was opened, false if not (e.g. pop-up blocked).
 */
const openTab = (url, name = "_blank", slugUrl) => {
  const win = window.open(url, name);

  if (win != null) {
    win.focus();

    const remoteUrl = new URL(url);

    window.addEventListener("message", event => {
      if (event.origin !== remoteUrl.origin){
        return;
      }
      if (event.data === "annolensPortalMeetingSlug|received"){
        clearTimeout(intervalId);
      }
    }, false);


    // send message in regular intervals until other tab is responding
    const intervalId = setInterval(() => {
      const message = `annolensPortalMeetingSlug|${slugUrl}`;
      win.postMessage(message, remoteUrl.origin);
    }, 1000);

    // after a certain amount of time, we think the other window will never be
    // ready, so don't send any more messages:
    setTimeout(() => clearInterval(intervalId), 1000*60);

    return true;

  } else {
    return false;
  }
};

module.exports = {
  openTab
};
