const MEETINGSTATUSES = {
  M_NOT_STARTED: "Meeting has not started yet",
  M_ONGOING: "Meeting is ongoing",
  M_ENDED: "Meeting has ended"
};
const M_NOT_STARTED = "M_NOT_STARTED";
const M_ONGOING = "M_ONGOING";
const M_ENDED = "M_ENDED";

const meetingStatus = m => {
  if (!m.realStartDateTime && !m.realEndDateTime) {
    return M_NOT_STARTED;
  } else if (!!m.realStartDateTime && !m.realEndDateTime) {
    return M_ONGOING;
  } else if (!!m.realStartDateTime && !!m.realEndDateTime) {
    return M_ENDED;
  }
};

const meetingHasStarted = m => meetingStatus(m) === M_ONGOING;
const meetingHasNotStarted = m => meetingStatus(m) === M_NOT_STARTED;
const meetingHasEnded = m => meetingStatus(m) === M_ENDED;

const iAmModerator = (meeting, auth) =>
  (meeting || {}).moderator &&
  meeting.moderator === ((auth || {}).user || {}).userId;

module.exports = {
  MEETINGSTATUSES,
  meetingStatus,
  meetingHasStarted,
  meetingHasNotStarted,
  meetingHasEnded,
  iAmModerator
};
