import { useAuth } from "./use-auth.js";

const API_SERVER = process.env.REACT_APP_API_SERVER;

function AuthButton() {
  let auth = useAuth();

  const logoutUrl = `${API_SERVER}/api/auth/logout`;

  const signout = _ =>
        auth.signout(_ => console.log("auth.signout finished"));

  return auth.user ? (
    <p>
      <a href={logoutUrl} onClick={signout}>
        <button className="block w-full h-full text-center shadow-light text-sm uppercase font-extrabold py-6 border-transparent hover:bg-blue-900">
          Sign out
        </button>
      </a>
    </p>
  ) : (
    <p>You are not logged in.</p>
  );
}

export default AuthButton;
