import React, { useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { base64DecodeUnicode } from "../utils/base64.js";
import * as storage from "../utils/storage";

import { useAuth } from "./use-auth.js";

/**
 * This component gets called by FusionAuth as a callback containing the
 * access_token in the "code" query param.
 */
const LoginCallback = props => {
  let history = useHistory();
  let location = useLocation();

  let auth = useAuth();
  function useQuery() {
    return new URLSearchParams(location.search);
  }

  let query = useQuery();
  // const userState = query.get("userState");

  useEffect(
    () => {
      if (!auth.user && !storage.local.getItem("accessToken")) {
        const code = query.get("code");
        const state = base64DecodeUnicode(query.get("state"));
        auth.signin({ code, state }, state => history.replace(state || "/"));
      }
    },
    [auth, history, query]
  );

  return (
    <div className="flex h-screen items-center justify-center cursor-wait">
      <div className="text-center text-3xl items-start">Please wait ...</div>
    </div>
  );
};

export default LoginCallback;
