// from https://www.base64encoder.io/javascript/
// btoa() only accepts 8 bit characters
export const base64EncodeUnicode = str => {
  if (!str) return str;

  // First we escape the string using encodeURIComponent to get the UTF-8 encoding of the characters,
  // then we convert the percent encodings into raw bytes, and finally feed it to btoa() function.
  const utf8Bytes = encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, function(
    match,
    p1
  ) {
    return String.fromCharCode("0x" + p1);
  });

  return btoa(utf8Bytes);
};

export const base64DecodeUnicode = str => {
  if (!str) return str;

  // Convert Base64 encoded bytes to percent-encoding, and then get the original string.
  const percentEncodedStr = atob(str)
    .split("")
    .map(function(c) {
      return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
    })
    .join("");

  return decodeURIComponent(percentEncodedStr);
};
