import React from "react";
import { queryCache, useMutation } from "react-query";
import {
  meetingHasNotStarted,
  meetingHasStarted,
  iAmModerator,
  meetingStatus
} from "../utils/meeting.js";
import { callStartMeeting, callStopMeeting } from "../utils/query.js";
import Button from "./Button.js";

const MeetingStartStopButton = ({ meeting, auth, queryKeys }) => {
  const [startMeeting] = useMutation(callStartMeeting, {
    onSuccess: _ => queryCache.refetchQueries(queryKeys)
  });

  const [stopMeeting] = useMutation(callStopMeeting, {
    onSuccess: _ => queryCache.refetchQueries(queryKeys)
  });

  return iAmModerator(meeting, auth) &&
    meeting.slug &&
    meetingHasNotStarted(meeting) ? (
    <Button
      colour="green"
      onClick={_ => startMeeting({ id: meeting._id })}
      text="START"
    />
  ) : meetingHasStarted(meeting) ? (
    <Button
      colour="green"
      onClick={_ => stopMeeting({ id: meeting._id })}
      text="STOP"
    />
  ) : !!meeting.realStartDateTime ? (
    <Button
      colour="green"
      onClick={_ => startMeeting({ id: meeting._id })}
      text="RESTART"
    />
  ) : (
    <div>{meetingStatus(meeting)}</div>
  );
};

export default MeetingStartStopButton;
