import React, { useEffect, useState } from "react";
import { useMutation, useQuery } from "react-query";
import { Link } from "react-router-dom";
import { Redirect, useParams } from "react-router-dom";
import { Formik, Form, Field, ErrorMessage } from "formik";

import Button from "./Button.js";
import {
  anonAxios,
  authedAxios,
  ERROR_NO_ACCESSTOKEN
} from "../axios/axiosHelpers.js";
import { useAuth } from "../auth/use-auth.js";
import {
  iAmModerator,
  meetingHasStarted,
  MEETINGSTATUSES,
  meetingStatus
} from "../utils/meeting.js";
import {
  goToMeeting,
  callAddParticipantToMeeting,
  callMeetingModerator,
  callGetUser
} from "../utils/query.js";
import { formatDateTime } from "../utils/l18n.js";
import { formatMeetingDuration } from "../utils/datetime.js";

const POLLING_INTERVAL_MS = 5000;
const errorClass = (e, t, f) => (e[f] && t[f] ? "border-2 border-red-400" : "");

const DEFAULT_GUEST_NAME = "Guest";

/**
 * This component will be shown if the URL is a slug meeting URL like
 * "/m/8495-9458-9990". Translate slug meeting URL given to user beforehand into
 * real meeting URL containing short-lived JWT token. Show "Go to meeting"
 * button
 */
const MeetingRedirect = ({ guestRoute }) => {
  let auth = useAuth();
  let { slug } = useParams();
  const [fetchError, setFetchError] = useState();
  const [meeting, setMeeting] = useState();
  const [tryCounter, setTryCounter] = useState(0);
  const [jwtExpired, setJwtExpired] = useState();
  const [redirected, setRedirected] = useState(false);
  const [addedAsParticipant, setAddedAsParticipant] = useState(false);
  const [moderatorDetails, setModeratorDetails] = useState();
  const [user, setUser] = useState();
  const [guestName, setGuestName] = useState(DEFAULT_GUEST_NAME);
  const [jitsiTabUrl, setJitsiTabUrl] = useState();
  const [jitsiError, setJitsiError] = useState();

  /* Signout on JWT expired */
  useEffect(() => {
    if (jwtExpired) auth.signout();
  }, [auth, jwtExpired]);

  /* Fetch status */
  const { isFetching, isLoading } = useQuery(
    ["meeting", slug],
    async (_, slug) =>
      (await guestRoute)
        ? anonAxios.get(`/api/meetings/byslug/${slug}/status`)
        : authedAxios.get(`/api/meetings/byslug/${slug}/status`),
    {
      refetchInterval: POLLING_INTERVAL_MS,
      onSuccess: ({ data }) => {
        setMeeting(data);
        setTryCounter(tryCounter + 1);
        setFetchError(undefined);
      },
      onError: err => {
        const message = (((err.response || {}).data || {}).error || {}).message;
        if (message) {
          setFetchError("Error fetching meeting: " + message);
          if (message === "jwt expired") setJwtExpired(true);
        } else if (err === ERROR_NO_ACCESSTOKEN) {
          setFetchError(`No access token, please log out and in again.`);
          alert("No access token, please log in again");
          auth.signout();
        } else setFetchError("Error: " + err);
      }
    }
  );

  useQuery(
    ["moderator", meeting],
    async (_, meeting) =>
      await callMeetingModerator({
        meetingId: meeting._id,
        moderatorId: meeting.moderator,
        asGuest: meeting.accessAsGuest
      }),
    {
      enabled: !!meeting && !moderatorDetails,
      onSuccess: ({ data }) => setModeratorDetails(data),
      onError: err => {
        const message = (((err.response || {}).data || {}).error || {}).message;
        if (message) {
          console.error(message);
        }
      }
    }
  );

  useEffect(() => {
    void (async function () {
      if (!user && auth.user) {
        try {
          setUser((await callGetUser({ id: auth.user.userId })).data);
        } catch (err) {
          console.error(err);
        }
      }
    })();
  }, [auth, user]);

  /* Add participant to meeting */
  const [addParticipant] = useMutation(callAddParticipantToMeeting, {
    onSuccess: _ => {
      setFetchError(undefined);
      setAddedAsParticipant(true);
    },
    onError: err => {
      if (err.response) {
        setFetchError("useQuery.onError: " + JSON.stringify(err.response));
      } else {
        setFetchError("useQuery other error:" + err.message);
      }
    }
  });

  useEffect(() => {
    async function updateParticipantList() {
      await addParticipant({
        meetingId: meeting._id,
        participantId: auth.user.userId
      });
    }
    if (meeting && (auth || {}).user && !addedAsParticipant) {
      updateParticipantList();
    }
  }, [meeting, auth, addParticipant, addedAsParticipant]);

  /* Moderators should be either redirected to the Jitsi meet or to
   * the details page, depending on whether meeting has started.
   * This is so the deep link works without too much clicking.
   */
  // TODO: this doesn't work yet, causes an endless loop for moderators when
  // closing Jitsi-Meet or opening portal tab
  // useEffect(() => {
  //   if (redirected) goToMeeting(meeting.slug);
  // }, [redirected, meeting, auth]);

  /* No data yet */
  if (!meeting) {
    return (
      <div
        className={`flex h-screen items-center justify-center ${
          isFetching || isLoading ? "cursor-wait" : ""
        }`}
      >
        {fetchError ? (
          <div className="text-red-400">{fetchError}</div>
        ) : (
          <div className="text-center text-3xl items-start">
            <Link to="/">Please wait...</Link>
          </div>
        )}
      </div>
    );
  }

  /* Backstage pass */
  if (iAmModerator(meeting, auth) && !redirected) {
    if (meetingHasStarted(meeting)) setRedirected(true);
    else return <Redirect to={`/meeting/${meeting.slug}`} />;
  }

  /* No freeloaders */
  if (meeting.accessAsGuest && guestRoute && !meeting.isGuestsAllowed) {
    return (
      <div className={"flex h-screen items-center justify-center"}>
        <div className="text-center text-3xl items-start underline">
          <Link to="/">Guests are not allowed in this meeting.</Link>
        </div>
      </div>
    );
  }

  /*
   * at this stage, there will be a meeting and user will be not the moderator
   */

  /* TODO: If logged in, /m/<slug>/guest route is wrong, go to non-guest route */
  // console.log('auth:', auth);
  // if ((auth||{}).user && guestRoute && !meeting.accessAsGuest){
  //   console.log('logged in to /guest');
  //   return <Redirect to={`/m/${meeting.slug}`} />
  // }

  /*
    logged in:
      yes:
            isStarted:
                yes: <button go to meeting>
                no: wait...

      no:
            guest route:
              yes: guests allowed:
                      yes: isStarted:wait/<button go to meeting>
                      (*)no: forbidden
              (*)no: redirect to Login with deepLink
  */

  const handleGotoMeetingButton = async meeting => {
    if (!meetingHasStarted(meeting)) return;
    const result = await goToMeeting(
      meeting.slug,
      guestRoute ? guestName || DEFAULT_GUEST_NAME : null,
      (tabOpened, url) =>
        setJitsiTabUrl({
          id: meeting._id,
          tabOpened,
          url: tabOpened ? null : url
        })
    );
    if (result) {
      setJitsiError({
        id: meeting._id,
        message: JSON.stringify(result)
      });
    }
  };

  if (guestRoute) {
    if (!meeting.isGuestsAllowed) return <div>GUESTS NOT ALLOWED</div>;
    // else wait/<button go to meeting>
  } else {
    /* Not logged in to /m/<slug>/guest: Login first */
    if (!(auth || {}).user && !meeting.accessAsGuest)
      return <Redirect to={`/m/${meeting.slug}`} />;
  }

  return (
    <>
      <div className="flex flex-col mt-1 ml-1 mr-1 md:mt-2 md:ml-8 md:mr-8">
        <div className="md:mt-4 mb-2 border rounded border-width-2 border-opacity-25">
          <div className="p-4 text-lg rounded-md ">{meeting.topic || "—"}</div>
          {meeting.description && (
            <div className="italic rounded-md p-4">{meeting.description}</div>
          )}
        </div>

        {moderatorDetails && (
          <div className="p-4 rounded-md border border-width-2 border-opacity-25">
            <div className="text-lg">Moderator:</div>
            <div className="p-4 rounded-md border border-width-2 border-opacity-25 break-all">
              {moderatorDetails.username
                ? `${moderatorDetails.username} (${moderatorDetails.email})`
                : moderatorDetails.email}
            </div>
          </div>
        )}

        <div className="mt-4 mb-2 p-4 border rounded border-width-2 border-opacity-25">
          <div className="text-lg">
            Current meeting status: {MEETINGSTATUSES[meetingStatus(meeting)]}
          </div>

          {meeting.accessAsGuest && (
            <Formik
              initialValues={{ guestName: "" }}
              validate={values => {
                const errors = {};
                if (values.guestName && values.guestName.length > 30)
                  errors.guestName = "Max 30 characters";
                setGuestName(values.guestName);
                return errors;
              }}
            >
              {({ errors, touched }) => (
                <Form>
                  <Field
                    className={`bg-yellow-200 rounded p-2 border-width-2 ${errorClass(
                      errors,
                      touched,
                      "guestName"
                    )}`}
                    type="text"
                    name="guestName"
                    placeholder="Name"
                  />
                  <div />
                  <ErrorMessage
                    name="guestName"
                    render={msg => <div className="text-red-400">{msg}</div>}
                  />
                </Form>
              )}
            </Formik>
          )}

          {!!meetingHasStarted(meeting) && (
            <>
              <div className="mt-2 ml-2 mb-2">
                open the conference
                <span className="font-bold">
                  {`${
                    meeting.accessAsGuest
                      ? " as guest"
                      : ` as ${
                          !!(user || {}).username
                            ? (user || {}).username
                            : ((auth || {}).user || {}).email
                        }`
                  }`}
                </span>
              </div>
              <div className="flex">
                <div className="ml-2 mr-2 ">
                  <Button
                    colour="green"
                    onClick={_ => handleGotoMeetingButton(meeting)}
                    text="Go to meeting"
                  />
                  {!!jitsiTabUrl &&
                    !jitsiTabUrl.tabOpened &&
                    jitsiTabUrl.id === meeting._id && (
                      <div className="p-4 rounded-md bg-red-300 break-all">
                        <p>Pop-up blocked, use</p>
                        <p>
                          <a className="underline" href={jitsiTabUrl}>
                            {jitsiTabUrl}
                          </a>
                        </p>
                      </div>
                    )}
                  {jitsiError && jitsiError.id === meeting._id && (
                    <div className="p-4 rounded-md bg-red-300 break-all">
                      <p>Error:</p>
                      <p>{jitsiError.message}</p>
                    </div>
                  )}
                </div>
              </div>
            </>
          )}
        </div>

        <div className="mt-4 mb-2 p-4 border rounded border-width-2 border-opacity-25">
          <div className="mt-2 p-2 border rounded border-2 border-opacity-25 break-all">
            {!!meeting.startDateTime && (
              <div>
                scheduled start: {formatDateTime(meeting.startDateTime)}
              </div>
            )}
            {!!meeting.endDateTime && (
              <div>scheduled end: {formatDateTime(meeting.endDateTime)}</div>
            )}
            {!!meeting.realStartDateTime && (
              <div>last start: {formatDateTime(meeting.realStartDateTime)}</div>
            )}
            {!!meeting.realEndDateTime && (
              <div>last end: {formatDateTime(meeting.realEndDateTime)}</div>
            )}
            {meeting.duration !== undefined && (
              <div>duration so far: {formatMeetingDuration(meeting)}</div>
            )}
            <div>
              room name:{" "}
              {`${
                meeting.roomName
                  ? '"' + meeting.roomName + '"'
                  : "(no room has been created yet)"
              }`}
            </div>
            <div>
              Guests allowed: &nbsp;{" "}
              <input
                type="checkbox"
                checked={meeting.isGuestsAllowed}
                disabled
              />
            </div>
          </div>
        </div>
      </div>
      {fetchError && <div className="text-red-400">{fetchError}</div>}
    </>
  );
};
export default MeetingRedirect;
