import React, { createContext, useContext, useState } from "react";
import { API_SERVER, authedAxios, anonAxios } from "../axios/axiosHelpers.js";
import * as storage from "../utils/storage";

const authContext = createContext();

// Provider component that wraps your app and makes auth object ...
// ... available to any child component that calls useAuth().

export const ProvideAuth = ({ children }) => {
  const auth = useProvideAuth();
  return <authContext.Provider value={auth}>{children}</authContext.Provider>;
};

// Hook for child components to get the auth object ...
// ... and re-render when it changes.
export const useAuth = () => {
  const context = useContext(authContext);
  if (!context) {
    throw new Error(`useAuth must be used within a ProvideAuth`);
  }
  return context;
};

// Provider hook that creates auth object and handles state
function useProvideAuth() {
  const [user, setUser] = useState(null);
  const [accessToken, setAccessToken] = useState(null);
  const [refreshToken, setRefreshToken] = useState(null);

  const updateUser = async (cb) => {
    const user = JSON.parse(storage.local.getItem("user"));
    const refreshToken = storage.local.getItem("refreshToken");
    if (user) {
      setUser(user);
    }
    if (refreshToken){
      setRefreshToken(refreshToken);
    }

    cb && cb();
  };

  const signin = async ({ code, state }, cb) => {
    try {
      if (!code) {
        return cb(state);
      }
      // get JWT
      const { data, status, statusText } = await anonAxios.get(
        `/api/auth/access_token?code=${code}`
      );

      if (status >= 400) {
        console.log("Error getting access_token:", status, statusText);
        return;
      }

      setAccessToken(data.access_token);
      storage.local.setItem("accessToken", data.access_token);
      setRefreshToken(data.refresh_token);
      storage.local.setItem("refreshToken", data.refresh_token);

      // exchange access_token for user object
      const { data: userData, status: statusUser } = await authedAxios.get(
        "/api/user"
      );
      if (statusUser >= 400) {
        console.log("Error getting user data:", statusUser);
        return;
      }
      setUser(userData);
      storage.local.setItem("user", JSON.stringify(userData));
    } catch (err) {
      let errMsg;
      if (err.response) {
        const { status } = err.response;
        console.log("Axios: error=", JSON.stringify(err.response));
        errMsg =
          (status ? `status=${status}; ` : "") +
          (err.response.data || {}).message;
      } else if (err.request) {
        console.log("Axios: no response");
        errMsg = "no response";
      } else {
        console.log("other Axios error:", err);
        errMsg = "other error";
      }
      console.log("errMsg:", errMsg);
      // TODO: show Toast instead of alert()
      errMsg && alert(errMsg);
      // be safe and sign out in case login has happened but user
      // was not registered to app
      signout(cb);
    }

    cb && cb(state);
  };

  const signout = cb => {
    console.log("signout called");
    setUser(null);
    storage.local.removeItem("user");
    storage.local.removeItem("accessToken");
    storage.local.removeItem("refreshToken");
    window.location.href = `${API_SERVER}/api/auth/logout`;
    cb && cb();
  };

  return { user, signin, signout, updateUser, accessToken, refreshToken };
}
