import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import parse from "date-fns/parse";
import parseISO from "date-fns/parseISO";
import format from "date-fns/format";
import { useMutation, queryCache } from "react-query";
import { callCreateMeeting } from "../utils/query.js";
import Button from "./Button.js";

const errorClass = (e, t, f) => (e[f] && t[f] ? "border-2 border-red-400" : "");

const MeetingsForm = ({
  initialMeeting = {},
  showDeleteButton = false,
  afterSubmit
}) => {
  const [mutate, { error }] = useMutation(callCreateMeeting, {
    onSuccess: _ => {
      queryCache.refetchQueries(["meetings"]);
      queryCache.refetchQueries(["meeting", initialMeeting.slug]);
    }
  });

  const doSubmit = async (
    {
      _id,
      startDate,
      startTime,
      endDate,
      endTime,
      topic,
      isGuestsAllowed,
      description
    },
    { setSubmitting }
  ) => {
    const startDateTime = parse(
      `${startDate} ${startTime}`,
      "yyyy-MM-dd HH:mm",
      new Date()
    );
    const endDateTime = parse(
      `${endDate} ${endTime}`,
      "yyyy-MM-dd HH:mm",
      new Date()
    );

    try {
      await mutate({
        _id,
        topic,
        startDateTime,
        endDateTime,
        isGuestsAllowed,
        description
      });
      setSubmitting(false);
      afterSubmit && afterSubmit();
    } catch (error) {
      console.log("mutation error:", error);
    }
  };

  error && console.log("error=", error);

  return (
    <div className="m-4">
      <Formik
        initialValues={{
          _id: initialMeeting._id,
          topic: initialMeeting.topic || "",
          startDate: initialMeeting.startDateTime
            ? format(parseISO(initialMeeting.startDateTime), "yyyy-MM-dd")
            : "",
          startTime: initialMeeting.startDateTime
            ? format(parseISO(initialMeeting.startDateTime), "HH:mm")
            : "",
          endDate: initialMeeting.endDateTime
            ? format(parseISO(initialMeeting.endDateTime), "yyyy-MM-dd")
            : "",
          endTime: initialMeeting.endDateTime
            ? format(parseISO(initialMeeting.endDateTime), "HH:mm")
            : "",
          isGuestsAllowed: initialMeeting.isGuestsAllowed || false,
          description: initialMeeting.description || ""
        }}
        onSubmit={doSubmit}
      >
        {({ errors, touched, isSubmitting }) => (
          <Form>
            <div className="p-2 flex flex-col">
              <div className="text-sm ">Topic</div>
              <Field
                className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                  errors,
                  touched,
                  "topic"
                )}`}
                type="text"
                name="topic"
                placeholder="Meeting Topic"
              />
              <ErrorMessage
                name="topic"
                render={msg => <div className="text-red-400">{msg}</div>}
              />
            </div>
            <div className="flex flex-col lg:flex-row lg:flex-wrap">
              <div className="p-2">
                <div className="text-sm ">Scheduled start</div>
                <Field
                  className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                    errors,
                    touched,
                    "startDate"
                  )}`}
                  type="date"
                  name="startDate"
                  placeholder="Start Date"
                />
                <Field
                  className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                    errors,
                    touched
                  )}`}
                  type="time"
                  name="startTime"
                  placeholder="Start Time"
                />
              </div>
              <div className="p-2">
                <div className="text-sm ">Scheduled end</div>
                <Field
                  className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                    errors,
                    touched
                  )}`}
                  type="date"
                  name="endDate"
                  placeholder="End Date"
                />
                <Field
                  className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                    errors,
                    touched
                  )}`}
                  type="time"
                  name="endTime"
                  placeholder="End Time"
                />
              </div>
              <div className="p-2">
                <label htmlFor="isGuestsAllowed">
                  <div className="text-sm ">Guests allowed&nbsp;</div>
                </label>
                <Field
                  id="isGuestsAllowed"
                  className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                    errors,
                    touched
                  )}`}
                  name="isGuestsAllowed"
                  type="checkbox"
                />
              </div>
            </div>
            <div className="p-2 flex flex-col">
              <div className="text-sm ">Description</div>
              <Field
                className={`bg bg-yellow-200 rounded p-2 ${errorClass(
                  errors,
                  touched,
                  "description"
                )}`}
                type="textarea"
                component="textarea"
                rows={3}
                name="description"
                placeholder="Meeting description"
              />
              <ErrorMessage name="name" component="div" />
            </div>
            <div className="flex justify-between flex-row flex-wrap">
              <div className="p-2">
                <Button colour="green" disabled={isSubmitting} type="submit">
                  Submit
                </Button>
              </div>
              {showDeleteButton && (
                <div className="p-2">
                  <Button colour="red">Delete</Button>
                </div>
              )}
              <div className="p-2">
                <Button
                  colour="red"
                  additionalClass="flex items-center"
                  onClick={afterSubmit}
                >
                  <i className="far fa-window-close text-xl"></i>
                  <span>&nbsp; Cancel</span>
                </Button>
              </div>
            </div>
            {error && <div className="text-red-400">{error.message}</div>}
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default MeetingsForm;
