import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router } from "react-router-dom";
import App from "./App";
import { ProvideAuth } from "./auth/use-auth.js";
import { I18nStateContext, I18nProvider } from "./i18n/use-i18n.js";
import { MessageProvider } from "@messageformat/react";

import reportWebVitals from "./reportWebVitals";
import "./tailwind.output.css";

const BASENAME = process.env.REACT_APP_BASENAME || "/";

ReactDOM.render(
  <React.StrictMode>
    <ProvideAuth>
      <I18nProvider>
        <I18nStateContext.Consumer>
          {i18n => (
            <>
            <MessageProvider messages={i18n.messages}>
              <Router basename={BASENAME}>
                <App />
              </Router>
            </MessageProvider>
            </>
          )}
        </I18nStateContext.Consumer>
      </I18nProvider>
    </ProvideAuth>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
