import React, { useState } from "react";
import { Link } from "react-router-dom";
import { meetingHasStarted, iAmModerator } from "../utils/meeting.js";
import { goToMeeting } from "../utils/query.js";
import MeetingStartStopButton from "./MeetingStartStopButton.js";
import {
  formatMeetingDuration,
  scheduledOrRealDateTime
} from "../utils/datetime.js";

const MeetingsTable = ({ auth, data }) => {
  const [jitsiTabUrl, setJitsiTabUrl] = useState();
  const [jitsiError, setJitsiError] = useState();

  const handleGotoMeetingButton = async meeting => {
    if (!meetingHasStarted(meeting)) return;
    const result = await goToMeeting(meeting.slug, undefined, (tabOpened, url) =>
      setJitsiTabUrl({
        id: meeting._id,
        tabOpened,
        url: tabOpened ? null : url
      })
    );
    if (result) {
      setJitsiError({
        id: meeting._id,
        message: JSON.stringify(result)
      });
    }
  };

  return (
    <div className="flex flex-col m-1 md:m-4 mt-1 ">
      {(data || []).map(meeting => (
        <div
          key={meeting._id}
          className="flex flex-wrap justify-between p-4 border rounded border-width-2 border-opacity-25 mb-2"
        >
          {/* Topic */}
          <div className="ml-1 md:ml-2 md:mr-2">
            {iAmModerator(meeting, auth) ? (
              /* Link to MeetingDetails */
              <Link to={`/meeting/${meeting.slug}`}>
                <div className="w-48 bg bg-green-400 hover:bg-green-600 rounded-md p-4 ">
                  {meeting.topic || "—"}
                </div>
              </Link>
            ) : (
              <div className="p-1 ">{meeting.topic || "—"}</div>
            )}
          </div>

          {/* Start */}
          <div className="flex flex-col">
            <div
              className={`ml-2 mr-2 ${
                !!meeting.realStartDateTime ? "italic" : ""
              }`}
            >
              {scheduledOrRealDateTime(
                meeting.startDateTime,
                meeting.realStartDateTime
              )}
            </div>

            {/* End */}
            {/*
          <div className="ml-2 mr-2">
            {scheduledOrRealDateTime(
              meeting.endDateTime,
              meeting.realEndDateTime
            )}
          </div>
          */}

            {/* Duration */}
            <div className="ml-2 mr-2">{formatMeetingDuration(meeting)}</div>
          </div>

          {/* Moderator Action */}
          {iAmModerator(meeting, auth) ? (
            <div className="ml-2 mr-2 ">
              <MeetingStartStopButton meeting={meeting} auth={auth} />
            </div>
          ) : (
            <div />
          )}

          {/* Join Action */}
          {!iAmModerator(meeting, auth) ? (
            <div className="ml-2 mr-2 ">
              <Link to={`/m/${meeting.slug}`}>
                <div className="w-48 text-center bg bg-green-400 hover:bg-green-600 uppercase rounded-md p-4 ">
                  Join meeting
                </div>
              </Link>
            </div>
          ) : (
            <div className="ml-2 mr-2 ">
              <button
                className={`w-48 text-center  uppercase rounded-md p-4 ${
                  !meetingHasStarted(meeting)
                    ? "bg bg-gray-400 cursor-not-allowed text-gray-600"
                    : "bg bg-green-400 hover:bg-green-600"
                }`}
                onClick={_ => handleGotoMeetingButton(meeting)}
                disabled={!meetingHasStarted(meeting)}
              >
                Open meeting
              </button>
              {!!jitsiTabUrl &&
                !jitsiTabUrl.tabOpened &&
                jitsiTabUrl.id === meeting._id && (
                  <div className="p-4 rounded-md bg-red-300 break-all">
                    <p>Pop-up blocked, use</p>
                    <p>
                      <a className="underline" href={jitsiTabUrl.url}>
                        {jitsiTabUrl.url}
                      </a>
                    </p>
                  </div>
                )}
              {jitsiError && jitsiError.id === meeting._id && (
                <div className="p-4 rounded-md bg-red-300 break-all">
                  <p>Error:</p>
                  <p>{jitsiError.message}</p>
                </div>
              )}
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default MeetingsTable;
