import React, { useState } from "react";
import Meetings from "./Meetings.js";
import MeetingsForm from "./MeetingsForm.js";
import { useMessageGetter } from "@messageformat/react";
import Button from "./Button.js";

const MeetingsPage = () => {
  const m = useMessageGetter("meetingDetails");
  const [addMeeting, setAddMeeting] = useState(false);

  return (
    <div>
      <div className="mt-4 mb-4 p-4 flex">
        <div className="ml-auto">
          <label htmlFor="addMeeting"></label>
          {!addMeeting && (
            <Button
              id="addMeeting"
              colour="white"
              onClick={_ => setAddMeeting(true)}
              additionalClass="p-4 border-2 rounded-md hover:bg-gray-100 flex items-center"
            >
              <i className="far fa-plus-square text-xl"></i>&nbsp;
              {m("addMeetingButton", "add")}
            </Button>
          )}
        </div>
      </div>

      <div className="m-1 p-1 md:m-4 md:p-4 border-2 rounded-md">
        {addMeeting && <MeetingsForm afterSubmit={_ => setAddMeeting(false)} />}
        <Meetings />
      </div>
    </div>
  );
};

export default MeetingsPage;
