import format from "date-fns/format";
import parseISO from "date-fns/parseISO";

/**
 * Creates a time duration map split into weeks, days, hours, ...
 * Note: months have a fixed duration of 30 days here.
 * Could be fixed with date-fns functions isLeapYear(), lastDayOfMonth().
 * @param d duration in seconds
 */
const dateSplit = (d = 0) => {
  var r = {}; // result
  var s = {
    // structure
    year: 31536000,
    month: 2592000,
    week: 604800,
    day: 86400,
    hour: 3600,
    minute: 60,
    second: 1
  };

  Object.keys(s).forEach(function (key) {
    r[key] = Math.floor(d / s[key]);
    d -= r[key] * s[key];
  });

  return r;
};

const optional = (v, unit) => (v ? `${v}${unit}` : "");

const formatMeetingDuration = meeting => {
  const splitDate = dateSplit(meeting.duration);
  return meeting.duration === 0
    ? "0m"
    : `${optional(splitDate.month, "mth")} ${optional(
        splitDate.week,
        "w"
      )} ${optional(splitDate.day, "d")} ${optional(
        splitDate.hour,
        "h"
      )} ${optional(splitDate.minute, "m")} ${optional(splitDate.second, "s")}`;
};

const scheduledOrRealDateTime = (scheduled, real) =>
  !!real
    ? format(parseISO(real), "d.M.yyyy HH:mm")
    : !!scheduled
    ? format(parseISO(scheduled), "d.M.yyyy HH:mm")
    : "—";

export { formatMeetingDuration, scheduledOrRealDateTime };
