import React, { useEffect } from "react";
import * as storage from "../utils/storage";
import { useHistory, useLocation } from "react-router-dom";
import { useAuth } from "./use-auth.js";
import { base64EncodeUnicode } from "../utils/base64.js";
import Select from "react-select";
import { useMessageGetter } from "@messageformat/react";
import { useI18nDispatch } from "../i18n/use-i18n.js";
import { options as languageOptions, defaultOption } from "../i18n/const.js";
import Button from "../components/Button.js";

const API_SERVER = process.env.REACT_APP_API_SERVER;

const BRANDING = process.env.REACT_APP_BRANDING || "Annolens";

function Login() {
  let history = useHistory();
  let location = useLocation();
  let auth = useAuth();
  let i18nDispatch = useI18nDispatch();
  const m = useMessageGetter("login");

  let { from } = location.state || {
      from: { pathname: location.pathname }
    } || { from: { pathname: "/" } };

  const deepLink =
    from.pathname && from.pathname !== "/"
      ? base64EncodeUnicode(from.pathname)
      : "";

  const authUrl = deepLink
    ? `${API_SERVER}/api/auth/login?deepLink=${deepLink}`
    : `${API_SERVER}/api/auth/login`;

  // browser refresh handling: need to repopulate useAuth from local storage:
  useEffect(() => {
    if (!auth.user && storage.local.getItem("user"))
      auth.updateUser(_ => history.replace(location.pathname));
  }, [auth, history, location]);

  return auth.user ? null : (
    <div>
      <div className="p-2 w-48 ml-auto">
        <Select
          width={"auto"}
          defaultValue={defaultOption}
          onChange={i18nDispatch}
          options={languageOptions}
        />
      </div>
      <div className="flex flex-col h-full justify-between">
        <div className="py-4" />
        <div className="text-center text-3xl">{m("welcome", { BRANDING })}</div>
        <div className="py-4" />
        <div className="text-center">
          <div className="text-xs">
            {m("info", { DEEPLINK: from.pathname })}
          </div>
        </div>

        <div className="flex justify-between mt-8">
          <div />
          <a href={authUrl}>
            <Button 
              colour="green"
              additionalClass="px-16 py-8 w-90"
            >
              {m("label")}
            </Button>
          </a>
          <div />
        </div>
      </div>
    </div>
  );
}

export default Login;
