import { anonAxios, authedAxios } from "../axios/axiosHelpers.js";
import { openTab } from "../utils/window.js";
import { createSlugUrl } from "../utils/slug.js";

const JITSI_SERVER = process.env.REACT_APP_JITSI_SERVER;

/*
 * Fetches the real meeting room name and opens its URL in a new tab.
 * Calls callback with whether tab could be opened and the URL.
 */
export const goToMeeting = async (slug, guestName, cb) => {
  try {
    const { data, status, statusText } = guestName
      ? await anonAxios.get(
          `${"/api/meetings/room/byslug/"}${slug}?name=${encodeURIComponent(
            guestName
          )}`
        )
      : await authedAxios.get(`${"/api/meetings/room/byslug/"}${slug}`);
    if (status < 400) {
      const url = `${JITSI_SERVER}/${encodeURIComponent(data.roomName)}?jwt=${
        data.jwt
      }`;
      const opened = openTab(url, data.roomName, createSlugUrl(slug));
      cb && cb(opened, url);

      return undefined;
    } else {
      return `${status}: ${statusText}`;
    }
  } catch (err) {
    if (err.response) {
      const { data, status } = err.response;
      console.log("Axios: error=", err.response);
      return (
        (status ? `status=${status}; ` : "") +
        ((data || {}).error || {}).message
      );
    } else if (err.request) {
      console.log("Axios: no response");
      return "no response";
    } else {
      console.log("other Axios error");
      return "other error";
    }
  }
};

/**
 * Get Meeting details through its slug.
 */
export const callMeetingBySlug = async ({ slug }) =>
  await authedAxios.get(`/api/meetings/byslug/${slug}`);

/**
 * Get meeting's participants' infos.
 */
export const callMeetingParticipants = async ({
  meetingId,
  meetingParticipants
}) =>
  authedAxios.post(`/api/user/participantInfos/${meetingId}`, {
    participantIds: meetingParticipants
  });

/**
 * Get meeting's moderator info.
 */
export const callMeetingModerator = async ({
  meetingId,
  moderatorId,
  asGuest
}) =>
  asGuest
    ? await anonAxios.post(`/api/meetings/${meetingId}/moderator`, {
        moderatorId
      })
    : await authedAxios.post(`/api/meetings/${meetingId}/moderator`, {
        moderatorId
      });

/**
 * Create a meeting.
 */
export const callCreateMeeting = async meeting =>
  await authedAxios.post("/api/meetings", {
    ...meeting
  });

/**
 * Start a meeting by its ID.
 */
export const callStartMeeting = async ({ id }) =>
  await authedAxios.put(`/api/meetings/${id}/start`, { _id: id });

/**
 * Stops a meeting by its ID.
 */
export const callStopMeeting = async ({ id }) =>
  await authedAxios.put(`/api/meetings/${id}/stop`, { _id: id });

export const callDeleteMeeting = async ({ id }) =>
  await authedAxios.delete(`/api/meetings/${id}`);

export const callAddParticipantToMeeting = async ({
  meetingId,
  participantId
}) =>
  await authedAxios.post(`/api/meetings/${meetingId}/participants`, {
    participantId
  });

/**
 * Get single user by ID
 */
export const callGetUser = async ({ id }) =>
  await authedAxios.get(`/api/user/byid/${id}`);

/**
 * Update single user by ID
 */
export const callUpdateUser = async ({ userId, email, username, fullName }) =>
  await authedAxios.put(`/api/user/${userId}`, {
    _id: userId,
    email,
    username,
    fullName
  });

/**
 * Get page of users.
 */
export const callGetPreviousUsersPage = async ({
  pageKey,
  perPage = 25,
  searchText = ""
}) =>
  await authedAxios.get(
    `/api/user/list?${pageKey ? `&previous=${pageKey}` : ""}${
      perPage ? `&per_page=${perPage}` : ""
    }&search=${searchText}`
  );

export const callGetNextUsersPage = async ({
  pageKey,
  perPage = 25,
  searchText = ""
}) =>
  await authedAxios.get(
    `/api/user/list?${pageKey ? `next=${pageKey}` : ""}${
      perPage ? `&per_page=${perPage}` : ""
    }&search=${searchText}`
  );

/**
 * Create a new user
 */
export const callUpsertUser = async user =>
  user._id
    ? await authedAxios.put(`/api/user/${user._id}`, {
        ...user
      })
    : await authedAxios.post("/api/user/new", {
        ...user
      });

export const callDeleteUser = async ({ _id }) =>
  await authedAxios.delete(`/api/user/${_id}`);
