import axios from "axios";
import * as storage from "../utils/storage";

const API_SERVER = process.env.REACT_APP_API_SERVER;

const ERROR_NO_ACCESSTOKEN = "ERROR_NO_ACCESSTOKEN";

/**
 * Private axios instance without interceptor.
 * Necessary so an expired refresh_token won't cause
 * refreshAccessToken() to be called again, causing an
 * endless loop.
 */
const refreshTokenAxios = axios.create({
  baseURL: API_SERVER
});

/**
 * Get new access_token with refresh_token or log out user if refresh_token has
 * expired as well.
 */
const refreshAccessToken = async () => {
  const refreshToken = storage.local.getItem("refreshToken");
  try {
    const { data } = await refreshTokenAxios.get(
      `/api/auth/access_token?refresh_token=${refreshToken}`
    );
    if (data.access_token) {
      storage.local.setItem("accessToken", data.access_token);
      return data.access_token;
    }
  } catch (error) {
    storage.local.removeItem("accessToken");
    storage.local.removeItem("refreshToken");
  }
};

/**
 * Axios instance with access_token header for routes that need authentication.
 */
const authedAxios = axios.create({
  baseURL: API_SERVER
});

/**
 * Inject current access_token into request.
 */
authedAxios.interceptors.request.use(
  config => {
    const accessToken = storage.local.getItem("accessToken");
    if (accessToken) {
      config.headers.access_token = accessToken;
      return config;
    } else {
      return Promise.reject(ERROR_NO_ACCESSTOKEN);
    }
  },
  error => {
    return Promise.reject(error);
  }
);

/**
 * Intercept query for status 401 errors, which means that the JWT is
 * expired; use the refresh token to acquire a new access token and
 * retry the original query again.
 */
authedAxios.interceptors.response.use(
  response => {
    return response;
  },
  async function (error) {
    const originalRequest = error.config;
    if ((error.response || {}).status === 401 && !originalRequest._retry) {
      // JWT expired, get new access_token with refresh_token and retry original request
      originalRequest._retry = true;
      const access_token = await refreshAccessToken();
      if (access_token) {
        return authedAxios(originalRequest);
      }
    }
    return Promise.reject(error);
  }
);

/**
 * Axios instance without access_token header.
 */
const anonAxios = axios.create({
  baseURL: API_SERVER
});

export { API_SERVER, authedAxios, anonAxios, refreshAccessToken, ERROR_NO_ACCESSTOKEN };
