import React from "react";
import { Link, Switch, Route } from "react-router-dom";
import styles from "./App.module.scss";
import Sidebar from "./ui/Sidebar.js";
import Topbar from "./ui/Topbar.js";
import UserList from "./components/UserList.js";
import Profile from "./components/Profile.js";
import MeetingsPage from "./components/MeetingsPage.js";
import MeetingRedirect from "./components/MeetingRedirect.js";
import MeetingDetails from "./components/MeetingDetails.js";
import Login from "./auth/Login.js";
import LoginCallback from "./auth/LoginCallback.js";
import Signout from "./auth/Signout.js";
import { useAuth } from "./auth/use-auth.js";
import { CUID_REG_STR } from "./utils/slug.js";

function App() {
  let auth = useAuth();
  return !auth.user ? (
    <Switch>
      {/* For guests, go directly to holding page */}
      <Route path={`/m/:slug(${CUID_REG_STR})/guest`}>
        <MeetingRedirect guestRoute={true} />
      </Route>
      <Route path="/oauth-callback">
        <LoginCallback />
      </Route>
      <Route path="/">
        <Login />
      </Route>
    </Switch>
  ) : (
    <div className="font-sans antialiased h-screen">
      <header className="z-50 w-full bg-blue-500 shadow flex items-center justify-between">
        {/*
            Home
           */}
        <div className="flex items-center text-center h-full w-full border-r border-blue-300">
          <span className="w-full text-white text-sm uppercase font-extrabold">
            <Link
              className="block text-center x shadow-light x py-6 border-transparent hover:bg-blue-900"
              to="/"
            >
              Home
            </Link>
          </span>
        </div>

        <div className="flex items-center text-center h-full w-full border-r border-blue-300">
          <span className="w-full text-white text-sm uppercase font-extrabold">
            <Signout />
          </span>
        </div>

        {/*
            Top navigation for small screens
          */}
        <div className="flex w-full sm:hidden md:hidden lg:hidden xl:hidden">
          <Topbar />
        </div>
      </header>

      {/*
        Main Grid
      */}
      <div className={styles.Content}>
        {/*
          Sidebar
        */}
        <aside className="hidden sm:block md:block lg:block xl:block">
          <div>
            <Sidebar />
          </div>
        </aside>

        {/*
          Main content area
        */}
        <main className="border border-blue-300">
          <Switch>
            <Route path="/oauth-callback">
              <LoginCallback />
            </Route>

            {/* For non-guests, must go through Login page before redirecting
            to holding page */}
            <Route path={`/m/:slug(${CUID_REG_STR})`}>
              <MeetingRedirect />
            </Route>

            <Route path={`/meeting/:slug(${CUID_REG_STR})`}>
              <MeetingDetails />
            </Route>

            <Route path="/user-list">
              <UserList />
            </Route>
            <Route path="/profile">
              <Profile />
            </Route>
            <Route path="/meetings">
              <MeetingsPage />
            </Route>

            <Route path="/login">
              <Login />
            </Route>

            {/*
            <Route path="/logout">
              <LoggedOut />
            </Route>
            */}

            <Route
              path="/"
              exact
              render={_ => (
                <div className="p-16 text-center">
                  Select one of the menus on the left or top
                </div>
              )}
            />

            <Route
              render={routeProps => (
                <div className="bg bg-red-200 p-16 text-center">
                  no such page {routeProps.location.pathname}
                </div>
              )}
            />
          </Switch>
        </main>
      </div>

      <footer className="border border-blue-300">Footer</footer>
    </div>
  );
}

export default App;
