import React, { useEffect, useState } from "react";
import { authedAxios } from "../axios/axiosHelpers.js";
import isAfter from "date-fns/isAfter";
import parseISO from "date-fns/parseISO";
import { useQuery } from "react-query";
import * as storage from "../utils/storage";

import { useAuth } from "../auth/use-auth.js";

import MeetingsTable from "./MeetingsTable.js";

const API_SERVER = process.env.REACT_APP_API_SERVER;
// refetch meetings every so often to get status of ongoing meetings
const POLLING_INTERVAL_MS = 60000;

const Meetings = () => {
  let auth = useAuth();
  let [jwtExpired, setJwtExpired] = useState();

  useEffect(() => {
    if (jwtExpired) {
      console.log("useEffect: JWT expired, calling signout");
      auth.signout(_ =>
        console.log("auth.signout finished due to expired JWT")
      );
    }
  }, [auth, jwtExpired]);

  const fetchMeetings = async _ => {
    const result = await authedAxios.get(`${API_SERVER}/api/meetings`);
    return result
      ? result.data.sort((a, b) =>
          isAfter(parseISO(a.startDateTime), parseISO(b.startDateTime)) ? -1 : 1
        )
      : [];
  };

  const { isFetching, isLoading, isError, data, error } = useQuery(
    ["meetings"],
    fetchMeetings,
    {
      refetchInterval: POLLING_INTERVAL_MS,
      onError: err => {
        if (err.response) {
          console.error("useQuery.onError:", err.response);
        } else {
          console.error("useQuery other error:", err.message);
        }
      }
    }
  );

  if (isError) {
    const responseDataMessageArray = (
      ((error.response || {}).data || {}).error || { message: [""] }
    ).message;
    if (!jwtExpired && !storage.local.getItem("accessToken")) {
      console.log("setJwtExpired");
      setJwtExpired(true);
    }

    if (error.message) {
      return (
        <div className="flex h-screen items-center justify-center">
          <div className="text-center text-3xl items-start">
            <div>Other Error: {error.message}</div>
          </div>
        </div>
      );
    }

    return (
      <div className="flex h-screen items-center justify-center">
        <div className="text-center text-3xl items-start">
          <div>Error: {JSON.stringify(responseDataMessageArray)}</div>
        </div>
      </div>
    );
  }

  const ownMeetings = (data || []).filter(
    m => m.moderator === auth.user.userId
  );
  const otherMeetings = (data || []).filter(
    m => m.moderator !== auth.user.userId
  );

  return (
    <>
      <div
        id="MeetingsTable"
        className={isLoading || isFetching ? "cursor-wait" : undefined}
      >
        {ownMeetings.length ? (
          <>
            <div className="font-bold ml-1 mt-1 md:ml-4 md:mt-4">
              You are moderator in these meetings:
            </div>
            <MeetingsTable auth={auth} data={ownMeetings} />
          </>
        ) : (
          <div className="text-lg text-center m-4">No own meetings</div>
        )}
        {otherMeetings.length ? (
          <>
            <div className="font-bold ml-4 mt-4">
              You are participant in these meetings:
            </div>
            <MeetingsTable auth={auth} data={otherMeetings} />
          </>
        ) : (
          <div className="text-lg text-center m-4"></div>
        )}
      </div>
      {isLoading && <div>loading...</div>}
    </>
  );
};
export default Meetings;
