import React from "react";

const Button = ({
  id,
  colour,
  text,
  additionalClass = "",
  type="button",
  disabled,
  children,
  onClick
}) => (
  <button
    id={id}
    className={`p-4 uppercase bg ${
      colour
        ? `bg-${colour}-400 ${!disabled ? `hover:bg-${colour}-600` : ""}`
        : ""
    } ${
      disabled ? "opacity-25 cursor-not-allowed" : ""
    } w-max rounded-md ${additionalClass}`}
    onClick={onClick}
    type={type}
    disabled={disabled}
  >
    {text}
    {children}
  </button>
);

export default Button;
