import React, { useState } from "react";
import { useQuery, queryCache } from "react-query";
import {
  callGetNextUsersPage,
  callGetPreviousUsersPage
} from "../utils/query.js";
import { useMessageGetter } from "@messageformat/react";
import Button from "./Button.js";
import UserForm from "./UserForm.js";
import { useDebounce } from "../utils/hooks.js";

const UserList = () => {
  const m = useMessageGetter("userList");
  const [searchText, setSearchText] = useState("");
  const debouncedSearchText = useDebounce(searchText, 500);
  // key to fetch page for, either "next" or "previous"
  const [pageKey, setPageKey] = useState({});
  // overall first ID to know when no previous page will be available
  const [firstId, setFirstId] = useState();
  // overall last ID to know when no next page will be available
  const [lastId, setLastId] = useState();
  // the actual data: pageData.items[]
  const [pageData, setPageData] = useState();
  const [fetchError, setFetchError] = useState();
  const [addUser, setAddUser] = useState(false);
  const [editUser, setEditUser] = useState(false);

  const { isFetching, isLoading } = useQuery(
    ["users", pageKey.next, pageKey.previous, debouncedSearchText],
    async (_, next, previous, searchText) => {
      return next
        ? await callGetNextUsersPage({ pageKey: next, searchText })
        : await callGetPreviousUsersPage({ pageKey: previous, searchText });
    },
    {
      keepPreviousData: true,
      onSuccess: ({ data }) => {
        if (searchText || data.items.length) {
          setPageData(data);
        }
        setFetchError(null);
        if (data.items.length) {
          if (!firstId) setFirstId(data.previous);
        } else if (!lastId) {
          setLastId(pageKey.next);
        }
      },
      onError: err => {
        const message = (
          ((err.response || {}).data || {}).error ||
          (err.response || {}).data ||
          {}
        ).message;
        if (message) {
          setFetchError(message);
        } else setFetchError(err);
      }
    }
  );

  const updateSearchText = s => {
    setFirstId(null);
    setLastId(null);
    setSearchText(s);
    setPageKey({});
  };

  return (
    <>
      {addUser ? (
        <UserForm afterSubmit={_ => setAddUser(false)} />
      ) : (
        <div className="mt-4 mb-4 p-4 flex">
          <div className="ml-auto">
            <label htmlFor="addUser"></label>
            <Button
              id="addUser"
              colour="white"
              onClick={_ => setAddUser(true)}
              additionalClass="p-4 border-2 rounded-md hover:bg-gray-100 flex items-center"
            >
              <i className="far fa-plus-square text-xl"></i>&nbsp;
              {m("addUserButton", "add")}
            </Button>
          </div>
        </div>
      )}

      {pageData && (
        <div style={{ width: "max-content" }}>
          <div className="m-4 flex rounded border border-width-2">
            <input
              value={searchText}
              onChange={e => updateSearchText(e.target.value)}
              placeholder="Search"
              className="p-2"
            />
            <Button
              id="clearSearch"
              colour="white"
              onClick={_ => updateSearchText("")}
            >
              <i className="far fa-window-close text-xl"></i>&nbsp;
            </Button>
          </div>
        </div>
      )}

      {!pageData || !pageData.items || !pageData.items.length ? (
        /* empty state */
        <div> nothing to show </div>
      ) : (
        /* data available */
        <div className="m-4">
          {pageData.items.map(user =>
            editUser && editUser === user._id ? (
              <React.Fragment key={user._id}>
                <UserForm
                  key={user._id}
                  afterSubmit={_ => {
                    setEditUser(false);
                    queryCache.refetchQueries(["users"]);
                  }}
                  initialUser={user}
                />
                <div className="p-4 ">
                  Roles:
                  {user.roles.sort().map(r => (
                    <div key={r} className="p-2">
                      <i className="fas fa-user-tag"></i>&nbsp;
                      {r}
                    </div>
                  ))}
                </div>
              </React.Fragment>
            ) : (
              <div
                key={user._id}
                className="p-2 grid grid-cols-1 md:grid-cols-2 xl:grid-cols-10 gap-2 rounded border border-width-2 border-opacity-25 mb-2"
              >
                <div className="xl:col-span-4">{user.email}</div>
                <div className="text-left md:text-right xl:text-left xl:col-span-2">
                  {user.username}
                </div>
                <div className="xl:col-span-3">{user.fullName}</div>
                <Button
                  id="editUser"
                  disabled={isFetching || isLoading}
                  onClick={_ => {
                    setEditUser(user._id);
                    // setMessage(undefined);
                  }}
                >
                  <div className="text-right">
                    <i className="fas fa-user-edit text-2xl"></i>
                    {user.roles.includes("admin") ? (
                      <i className="fas fa-user-shield text-2xl"></i>
                    ) : null}
                  </div>
                </Button>
              </div>
            )
          )}
        </div>
      )}

      {/* paging controls */}
      {pageData && (
        <div className="flex m-4">
          <Button
            colour="green"
            disabled={pageData.previous === firstId}
            additionalClass="mr-4"
            onClick={_ => setPageKey({ previous: pageData.previous })}
          >
            Previous
          </Button>
          <Button
            colour="green"
            disabled={pageData.next === lastId}
            onClick={_ => setPageKey({ next: pageData.next })}
          >
            Next
          </Button>
        </div>
      )}

      {/* messages */}
      {isLoading && <div>loading...</div>}
      {fetchError ? <div>Error: {JSON.stringify(fetchError)}</div> : null}
    </>
  );
};

export default UserList;
