import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { useMutation, queryCache } from "react-query";
import { callUpsertUser, callDeleteUser } from "../utils/query.js";
import Button from "./Button.js";

const errorClass = (e, t, f) => (e[f] && t[f] ? "border-2 border-red-400" : "");

const UserForm = ({ initialUser = {}, afterSubmit }) => {
  const [mutateError, setMutateError] = useState();

  const [mutate, { error }] = useMutation(callUpsertUser, {
    onSuccess: _ => {
      queryCache.refetchQueries(["users"]);
      setMutateError(null);
      afterSubmit && afterSubmit();
    },
    onError: err => {
      setMutateError(err.response.data);
      console.log("onError: ", err.response.data);
    }
  });

  const doSubmit = async (
    { _id, username, email, fullName },
    { setSubmitting }
  ) => {
    try {
      await mutate({
        _id,
        username,
        email,
        fullName
      });
      setSubmitting(false);
    } catch (error) {
      console.log("mutation error:", error);
    }
  };

  const [deleteMutation] = useMutation(callDeleteUser, {
    onSuccess: _ => {
      queryCache.refetchQueries(["users"]);
      setMutateError(null);
      afterSubmit && afterSubmit();
    },
    onError: err => {
      setMutateError(err.response.data);
      console.log("delete onError: ", err.response.data);
    }
  });

  const handleDelete = async _id => {
    try {
      await deleteMutation({ _id });
    } catch (error) {
      console.log("delete error:", error);
    }
  };

  return (
    <div className="m-4 p-4 rounded border border-2 md:w-5/6 lg:w-2/3 max-w-prose">
      <Formik
        initialValues={{
          _id: initialUser._id,
          username: initialUser.username || "",
          email: initialUser.email || "",
          fullName: initialUser.fullName || ""
        }}
        validate={values => {
          const errors = {};
          if (!values.email || !values.email.length > 3)
            errors.email = "Required";
          if (values.fullName && values.fullName.length > 30)
            errors.fullName = "Max 30 characters";
          return errors;
        }}
        onSubmit={doSubmit}
      >
        {({ values, errors, touched, isSubmitting }) => (
          <Form>
            <div className="mb-4 grid grid-cols-1 md:grid-cols-4">
              <div className="">User name&nbsp;</div>
              <Field
                className={`md:col-span-3 bg bg-yellow-200 rounded p-2 ${errorClass(
                  errors,
                  touched,
                  "username"
                )}`}
                type="text"
                name="username"
                placeholder="User name"
              />
              <div />
              <ErrorMessage
                name="username"
                render={msg => <div className="text-red-400">{msg}</div>}
              />
            </div>

            <div className="mb-4 grid grid-cols-1 md:grid-cols-4">
              <div className="">E-mail&nbsp;</div>
              <Field
                className={`md:col-span-3 bg bg-yellow-200 rounded p-2 ${errorClass(
                  errors,
                  touched,
                  "email"
                )}`}
                type="text"
                name="email"
                placeholder="E-mail"
              />
              <div />
              <ErrorMessage
                name="email"
                render={msg => <div className="text-red-400">{msg}</div>}
              />
            </div>

            <div className="mb-4 grid grid-cols-1 md:grid-cols-4">
              <div className="">Full name&nbsp;</div>
              <Field
                className={`md:col-span-3 bg bg-yellow-200 rounded p-2 ${errorClass(
                  errors,
                  touched,
                  "fullName"
                )}`}
                type="text"
                name="fullName"
                placeholder="Full name"
              />
              <div />
              <ErrorMessage
                name="fullName"
                render={msg => <div className="text-red-400">{msg}</div>}
              />
            </div>

            {/* Buttons */}
            <div className="flex justify-between flex-row flex-wrap">
              <div className="p-2">
                <Button colour="green" disabled={isSubmitting} type="submit">
                  Submit
                </Button>
              </div>
              <div className="p-2">
                <Button
                  colour="red"
                  additionalClass="flex items-center"
                  onClick={afterSubmit}
                >
                  <i className="far fa-window-close text-xl"></i>
                  <span>&nbsp; Cancel</span>
                </Button>
              </div>
              <div className="p-2">
                <Button
                  colour="red"
                  additionalClass="flex items-center"
                  onClick={_ => handleDelete(values._id)}
                >
                  <i className="far fa-trash-alt text-xl"></i>
                  <span>&nbsp; Delete</span>
                </Button>
              </div>
            </div>

            {(mutateError || error) && (
              <div className="text-red-400">
                {(mutateError || error).message}
              </div>
            )}
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default UserForm;
