import React, { useState, useEffect } from "react";
import { useMutation, useQuery, queryCache } from "react-query";
import { Link, useHistory, useParams } from "react-router-dom";
import { createSlugUrl, createSlugUrlForGuests } from "../utils/slug.js";
import { formatDateTime } from "../utils/l18n.js";
import { formatMeetingDuration } from "../utils/datetime.js";
import MeetingStartStopButton from "./MeetingStartStopButton.js";
import MeetingsForm from "./MeetingsForm.js";
import { useMessageGetter } from "@messageformat/react";
import { ERROR_NO_ACCESSTOKEN } from "../axios/axiosHelpers.js";

import { useAuth } from "../auth/use-auth.js";
import {
  meetingHasNotStarted,
  meetingHasStarted,
  MEETINGSTATUSES,
  meetingStatus,
  iAmModerator
} from "../utils/meeting.js";
import {
  goToMeeting,
  callDeleteMeeting,
  callMeetingBySlug,
  callMeetingParticipants,
  callGetUser
} from "../utils/query.js";
import Button from "./Button.js";

/**
 * Component to edit meeting details
 */
const MeetingDetails = () => {
  const auth = useAuth();
  const history = useHistory();
  const { slug } = useParams();
  const m = useMessageGetter("meetingDetails");
  const [fetchError, setFetchError] = useState();
  const [meeting, setMeeting] = useState();
  const [editMeeting, setEditMeeting] = useState(false);
  const [participantDetails, setParticipantDetails] = useState();
  const [user, setUser] = useState();
  const [jitsiTabUrl, setJitsiTabUrl] = useState();
  const [jitsiError, setJitsiError] = useState();

  const { isLoading } = useQuery(
    ["meeting", slug],
    async (_, slug) => await callMeetingBySlug({ slug }),
    {
      onSuccess: ({ data }) => {
        setMeeting(data);
        setFetchError(null);
      },
      onError: err => {
        const message = (
          ((err.response || {}).data || {}).error ||
          (err.response || {}).data ||
          {}
        ).message;
        if (message) {
          setFetchError(message);
        } else if (err === ERROR_NO_ACCESSTOKEN) {
          setFetchError(`No access token, please log out and in again.`);
          alert("No access token, please log in again");
          auth.signout();
        } else setFetchError("Error: " + err);
      }
    }
  );

  useQuery(
    ["participants", meeting],
    async (_, meeting) =>
      await callMeetingParticipants({
        meetingId: meeting._id,
        meetingParticipants: meeting.participants
      }),
    {
      enabled: !!meeting && (meeting.participants || []).length,
      onSuccess: ({ data }) => setParticipantDetails(data),
      onError: err => {
        const message = (((err.response || {}).data || {}).error || {}).message;
        if (message) {
          console.error(message);
        }
      }
    }
  );

  const [deleteMeeting] = useMutation(callDeleteMeeting, {
    onSuccess: _ => {
      queryCache.refetchQueries(["meetings"]);
      history.goBack();
    },
    onError: err => {
      if (err.response) {
        setFetchError("useQuery.onError: " + JSON.stringify(err.response));
      } else {
        setFetchError("useQuery other error:" + err.message);
      }
    }
  });

  useEffect(() => {
    void (async function () {
      if (!user && auth.user) {
        try {
          setUser((await callGetUser({ id: auth.user.userId })).data);
        } catch (err) {
          console.error(err);
        }
      }
    })();
  }, [auth, user]);

  const handleGotoMeetingButton = async meeting => {
    if (!meetingHasStarted(meeting)) return;
    const result = await goToMeeting(
      meeting.slug,
      undefined,
      (tabOpened, url) =>
        setJitsiTabUrl({
          id: meeting._id,
          tabOpened,
          url: tabOpened ? null : url
        })
    );
    if (result) {
      setJitsiError({
        id: meeting._id,
        message: JSON.stringify(result)
      });
    }
  };

  return (
    <>
      <div className="m-4 mb-2">
        <Button
          colour="white"
          additionalClass="border-2 hover:bg-gray-100 flex items-center"
          onClick={_ => history.goBack()}
          text={<span>&lt;&mdash; Back</span>}
        />
      </div>
      {!!meeting && (
        <div>
          <div className="flex flex-col mt-1 ml-1 mr-1 md:mt-2 md:ml-8 md:mr-8">
            <div className="mt-4 mb-2 border rounded border-width-2 border-opacity-25">
              <Link to={createSlugUrl(meeting.slug)}>
                <div className="p-4 text-lg hover:bg-gray-100 rounded-md ">
                  {meeting.topic || "—"}
                </div>
              </Link>
              {meeting.description && (
                <div className="italic hover:bg-green-400 rounded-md p-4">
                  {meeting.description}
                </div>
              )}
            </div>

            <div className="mt-4 mb-2 p-4 border rounded border-width-2 border-opacity-25">
              <div className="text-lg">
                Current meeting status:{" "}
                {MEETINGSTATUSES[meetingStatus(meeting)]}
              </div>

              <div className="flex">
                {!iAmModerator(meeting, auth) && (
                  <Link to={createSlugUrl(meeting.slug)}>
                    <div className="w-48 text-center bg bg-green-400 hover:bg-green-600 uppercase rounded-md p-4 ">
                      Join meeting
                    </div>
                  </Link>
                )}
                {iAmModerator(meeting, auth) && meetingHasStarted(meeting) && (
                  <div className="p-2">
                    <Button
                      colour="green"
                      onClick={_ => handleGotoMeetingButton(meeting)}
                    >
                      Open meeting
                    </Button>
                    {!!jitsiTabUrl &&
                      !jitsiTabUrl.tabOpened &&
                      jitsiTabUrl.id === meeting._id && (
                        <div className="p-4 rounded-md bg-red-300 break-all">
                          <p>Pop-up blocked, use</p>
                          <p>
                            <a className="underline" href={jitsiTabUrl}>
                              {jitsiTabUrl}
                            </a>
                          </p>
                        </div>
                      )}
                    {jitsiError && jitsiError.id === meeting._id && (
                      <div className="p-4 rounded-md bg-red-300 break-all">
                        <p>Error:</p>
                        <p>{jitsiError.message}</p>
                      </div>
                    )}
                  </div>
                )}
                <div className="p-2">
                  <MeetingStartStopButton
                    meeting={meeting}
                    auth={auth}
                    queryKeys={["meeting", slug]}
                  />
                </div>
                {meetingHasNotStarted(meeting) && !meetingHasStarted(meeting) && (
                  <div className="p-2">
                    <Button
                      colour="red"
                      onClick={_ =>
                        deleteMeeting({
                          id: meeting._id
                        })
                      }
                    >
                      Delete
                    </Button>
                  </div>
                )}
              </div>
            </div>

            <div className="mt-4 mb-2 p-4 border rounded border-width-2 border-opacity-25">
              <label htmlFor="editMeeting"></label>
              {!editMeeting && (
                <Button
                  id="editMeeting"
                  colour="white"
                  onClick={_ => setEditMeeting(true)}
                  additionalClass="border-2 hover:bg-gray-100 flex items-center"
                >
                  <i className="fas fa-edit text-2xl"></i>&nbsp;
                  {m("editButton", "edit")}
                </Button>
              )}

              {editMeeting && (
                <MeetingsForm
                  initialMeeting={meeting}
                  afterSubmit={_ => setEditMeeting(false)}
                />
              )}

              <div className="mt-2 p-2 border rounded border-2 border-opacity-25">
                {!!meeting.startDateTime && (
                  <div>
                    scheduled start: {formatDateTime(meeting.startDateTime)}
                  </div>
                )}
                {!!meeting.endDateTime && (
                  <div>
                    scheduled end: {formatDateTime(meeting.endDateTime)}
                  </div>
                )}
                {!!meeting.realStartDateTime && (
                  <div>
                    last start: {formatDateTime(meeting.realStartDateTime)}
                  </div>
                )}
                {!!meeting.realEndDateTime && (
                  <div>last end: {formatDateTime(meeting.realEndDateTime)}</div>
                )}
                {meeting.duration !== undefined && (
                  <div>duration so far: {formatMeetingDuration(meeting)}</div>
                )}
                <div>
                  room name:{" "}
                  {`${
                    meeting.roomName
                      ? '"' + meeting.roomName + '"'
                      : "(no room has been created yet)"
                  }`}
                </div>
                <div>
                  Guests allowed: &nbsp;{" "}
                  <input
                    type="checkbox"
                    checked={meeting.isGuestsAllowed}
                    disabled
                  />
                </div>
              </div>
            </div>

            <div className="mt-4 mb-2 p-4 border rounded border-width-2 border-opacity-25 italic">
              <div className="text-lg">E-mail template:</div>
              <div className="ml-4 break-all">
                Dear meeting participant,
                <p />
                you are invited to join my meeting
                {!!meeting.startDateTime &&
                  ` starting at ${formatDateTime(meeting.startDateTime)} `}
                {!!meeting.endDateTime &&
                  ` and ending at ${formatDateTime(meeting.endDateTime)} `}
                <p />
                <div className="mt-2">
                  If you are a registered user of{" "}
                  {process.env.REACT_APP_BRANDING}, you can join through this
                  direct link:
                  <div className="ml-2">{createSlugUrl(meeting.slug)}</div>
                </div>
                <p />
                {meeting.isGuestsAllowed ? (
                  <>
                    <div className="mt-2">
                      Or you can join as a guest with this link:
                    </div>
                    <div className="ml-2">
                      {createSlugUrlForGuests(meeting.slug)}
                    </div>
                  </>
                ) : null}
                <p />
                {meeting.description && (
                  <>
                    <div className="mt-2">This meeting will be about:</div>
                    <div className="ml-2">{meeting.description}</div>
                  </>
                )}
                <p />
                <div className="pt-2">Yours faithfully,</div>
                <p />
                {`${
                  user && user.username
                    ? `${user.username} (${auth.user.email})`
                    : auth.user.email
                }`}
              </div>
            </div>

            {participantDetails && !!participantDetails.length && (
              <div className="p-4 rounded-md border border-width-2 border-opacity-25 break-all">
                <div className="text-lg italic">Participants:</div>
                {participantDetails.map(p => (
                  <div
                    key={p.email}
                    className="mt-1 mb-1 p-4 rounded-md border border-width-2 border-opacity-25"
                  >
                    {p.username ? `${p.username} (${p.email})` : p.email}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      )}
      {isLoading && <div>loading...</div>}
      {fetchError ? <div>Error: {JSON.stringify(fetchError)}</div> : null}
    </>
  );
};
export default MeetingDetails;
