import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { useMutation, useQuery, queryCache } from "react-query";
import { useAuth } from "../auth/use-auth.js";
import { useMessageGetter } from "@messageformat/react";
import { callGetUser, callUpdateUser } from "../utils/query.js";
import Button from "./Button.js";

const errorClass = (e, t, f) => (e[f] && t[f] ? "border-2 border-red-400" : "");

const Profile = props => {
  let auth = useAuth();
  const m = useMessageGetter("profile");
  const [editUser, setEditUser] = useState(false);
  const [user, setUser] = useState({
    email: auth.user.email || "",
    username: auth.user.username || "",
    fullName: auth.user.fullName || ""
  });
  const [userError, setUserError] = useState();
  const [message, setMessage] = useState();
  const [userEditError, setUserEditError] = useState();

  const [mutate] = useMutation(callUpdateUser, {
    onSuccess: newData => {
      setEditUser(false);
      if (newData.data.user.email !== user.email) {
        setMessage(m("emailChanged"));
      }
      setUserEditError(undefined);
    },
    onError: err => {
      const message = ((err.response || {}).data || {}).message;
      if (message) {
        setUserEditError(message);
      } else setUserEditError(err);
    }
  });

  const doSubmit = async (userData, { setSubmitting }) => {
    try {
      await mutate(userData);
      setSubmitting(false);
    } catch (error) {
      console.log("mutation error:", error);
    }
  };

  const { isFetching, isLoading } = useQuery(
    ["user", auth.user.userId],
    async (_, id) => await callGetUser({ id }),
    {
      onSuccess: ({ data }) => {
        setUser(data);
        setUserError(undefined);
      },
      onError: err => {
        const message = (((err.response || {}).data || {}).error || {}).message;
        if (message) {
          setUserError(message);
        } else setUserError(err);
      }
    }
  );

  return (
    <div
      className={`flex flex-col mt-2 ml-8 mr-8 ${
        isFetching || isLoading ? "cursor-wait" : ""
      }`}
    >
      <div className="ml-auto">
        <label htmlFor="editUser"></label>
        {!editUser && (
          <Button
            id="editUser"
            disabled={isFetching || isLoading}
            onClick={_ => {
              setEditUser(true);
              setMessage(undefined);
            }}
            additionalClass="border-2 flex items-center"
          >
            <i className="fas fa-user-edit text-2xl"></i>&nbsp;
            {m("editUserButton", "add")}
          </Button>
        )}
      </div>

      {!!message && (
        <div className="mt-4 p-4 rounded border border-width-2 bg-orange-200">
          {message}
        </div>
      )}

      {user && (
        <Formik
          initialValues={{
            userId: auth.user.userId,
            email: user.email,
            username: user.username,
            fullName: user.fullName
          }}
          enableReinitialize={true}
          validate={values => {
            const errors = {};
            if (!values.email) {
              errors.email = "Required";
            } else if (
              !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
            ) {
              errors.email = "Invalid email address";
            }
            if (values.fullName && values.fullName.length > 30)
              errors.fullName = "Max 30 characters";
            return errors;
          }}
          onSubmit={doSubmit}
        >
          {({ errors, touched, isSubmitting, resetForm }) => (
            <Form>
              <div className="mt-4 mb-2 border rounded border-width-2 border-opacity-25">
                {!auth.user ? null : (
                  <>
                    <div className="p-4 flex items-center">
                      <i className="fas fa-user"></i>&nbsp;
                      <Field
                        disabled={!editUser}
                        className={`bg ${
                          editUser ? "bg-yellow-200" : ""
                        } rounded p-2 ${errorClass(
                          errors,
                          touched,
                          "username"
                        )} flex-1`}
                        type="text"
                        name="username"
                        placeholder="Username"
                      />
                      <ErrorMessage
                        name="username"
                        render={msg => (
                          <div className="text-red-400">{msg}</div>
                        )}
                      />
                    </div>
                    <div className="p-4 flex items-center flex-grow">
                      <i className="fas fa-at"></i>&nbsp;
                      <Field
                        disabled={!editUser}
                        className={`bg ${
                          editUser ? "bg-yellow-200" : ""
                        } rounded p-2 ${errorClass(
                          errors,
                          touched,
                          "email"
                        )} flex-1`}
                        type="text"
                        name="email"
                        placeholder="e-mail"
                      />
                      <ErrorMessage
                        name="email"
                        render={msg => (
                          <div className="text-red-400">{msg}</div>
                        )}
                      />
                    </div>
                    <div className="p-4 flex items-center flex-grow">
                      <Field
                        disabled={!editUser}
                        className={`bg ${
                          editUser ? "bg-yellow-200" : ""
                        } rounded p-2 ${errorClass(
                          errors,
                          touched,
                          "fullName"
                        )} flex-1`}
                        type="text"
                        name="fullName"
                        placeholder="Full name"
                      />
                      <ErrorMessage
                        name="fullName"
                        render={msg => (
                          <div className="text-red-400">{msg}</div>
                        )}
                      />
                    </div>
                    <div className="p-4 ">
                      Roles:
                      {(user.roles||[]).sort().map(r => <div key={r} className="p-2">
                      <i className="fas fa-user-tag"></i>&nbsp;
                      {r}
                      </div>)}
                    </div>
                  </>
                )}

                {editUser && (
                  <>
                    <div className="m-2 flex justify-between flex-row flex-wrap">
                      <div className="p-2">
                        <Button
                          colour="green"
                          disabled={isSubmitting}
                          type="submit"
                        >
                          Submit
                        </Button>
                      </div>
                      <div className="p-2">
                        <Button
                          colour="red"
                          disabled={isSubmitting}
                          onClick={_ => {
                            resetForm();
                            queryCache.clear();
                            setEditUser(false);
                          }}
                          additionalClass="flex items-center"
                          type="reset"
                        >
                          <i className="far fa-window-close text-xl"></i>
                          <span>&nbsp; Cancel</span>
                        </Button>
                      </div>
                    </div>
                  </>
                )}
              </div>

              {userError && (
                <div className="text-red-400">{userError.message}</div>
              )}

              {userEditError && (
                <div className="text-red-400">{userEditError}</div>
              )}
            </Form>
          )}
        </Formik>
      )}
      <div>{isLoading && "loading..."}</div>
    </div>
  );
};
export default Profile;
